import './App.css';
import Home from './pages/home';
import Nav from './components/nav';
import KBZPaySDK from './kbzpay-sdk/js-sdk.esm';
import { Routes, Route, useNavigate, useLocation } from "react-router-dom";
import { useEffect, useMemo, useState } from 'react';
import ReservationTermAndCondition from './pages/reservation-term-condition';
import GLOBAL from './utils/global';
import ReservationReview from './pages/reservation-review';
import { AnimatePresence } from 'framer-motion';
import ReservationConfirmed from './pages/reservation-confirmed';
import ReservationHistoryList from './pages/reservation-history-list';
import ReservationHistoryDetail from './pages/reservation-history-detail';
import RoomDetail from './pages/room-detail';
import { BookingContext } from './context/booking-context';
import moment from 'moment';




export const routes = {
  Index: {
    path: '/'
  },
  RoomDetail: {
    path: '/room'
  },
  ReservationTermAndCondition: {
    path: '/reservation-term-and-condition'
  },
  ReservationReview: {
    path: '/reservation-review'
  },
  ReservationConfirmed: {
    path: '/reservation-confirmed'
  },
  ReservationHistoryDetail: {
    path: '/reservation-history-detail'
  },
  ReservationHistoryList: {
    path: '/reservation-histories'
  }
};





function App() {

  const location = useLocation();
  const navigate = useNavigate();
  const minDate = useMemo(() => {
    return moment().format('YYYY-MM-DD');
  });
  const [booking, setBooking] = useState({
    checkInDate: minDate,
    checkOutDate: minDate,
    adult: 1,
    child: 0,
    coupon: '',
    rooms: {}
  })
  const [availableRooms, setAvailableRooms] = useState([]);



  useEffect(() => {
    navigate('/');

    // KBZPaySDK.native("getMiniAppToken", { appId: GLOBAL.KBZPAY_MARCHANT_ID }).then(res => {
    //   console.log(res);
    // })
  }, [])

  return (
    <BookingContext.Provider value={{
      booking,
      setBooking,
      availableRooms,
      setAvailableRooms
    }}>
      <div className='flex flex-col w-full h-full bg-white items-center overflow-hidden'>
        <div className='flex flex-col w-full h-full md:w-[30vw]'>
          <Nav />
          <div className='w-full h-full overflow-hidden relative flex items-center justify-center'>
            <AnimatePresence mode='wait' initial={false}>
              <Routes>
                <Route index element={<Home key={'home'} />} />
                <Route path={routes.RoomDetail.path} element={<RoomDetail key={0} />} />
                <Route path={routes.ReservationTermAndCondition.path} element={<ReservationTermAndCondition key={1} />} />
                <Route path={routes.ReservationReview.path} element={<ReservationReview key={2} />} />
                <Route path={routes.ReservationConfirmed.path} element={<ReservationConfirmed key={3} />} />
                <Route path={routes.ReservationHistoryList.path} element={<ReservationHistoryList key={4} />} />
                <Route path={routes.ReservationHistoryDetail.path} element={<ReservationHistoryDetail key={5} />} />
              </Routes>
            </AnimatePresence>
          </div>
        </div>
      </div>
    </BookingContext.Provider>
  )
}

export default App;
