import { GeneralInvokeOption } from 'src/types';
import { getRealRoute, encodeUrlQuery, checkUrl } from '../utils';
import MacleJSBridge from 'src/bridges';
import { APINames, CURRENT_ROUTE, getGlobal } from 'src/shared';

export interface RedirectToOption extends Partial<GeneralInvokeOption> {
  url: string;
}

/**
 * ```js
 *   ma.miniProgram.redirectTo({
 *     url: 'example?id=1',
 *     success: function(res) {},
 *     fail: function(res) {},
 *     complete: function(res) {},
 *   })
 * ```
 */
export function redirectTo(option: RedirectToOption): void {
  if (!option.url) {
    console.error(
      'redirectTo fail,parameter error:parameter.url should be String instead of Undefined.'
    );
    return;
  }
  const fixedOption = option;
  fixedOption.url = getRealRoute(
    getGlobal(CURRENT_ROUTE) as string,
    fixedOption.url
  );
  fixedOption.url = encodeUrlQuery(fixedOption.url);

  if (checkUrl(APINames.MiniProgram.REDIRECT_TO, fixedOption)) {
    MacleJSBridge.invokeMethod(APINames.MiniProgram.REDIRECT_TO, fixedOption);
  }
}
