import { motion } from 'framer-motion';
import Carousal from '../components/common/carosal';
import { Check, Plus, Minus } from '@icon-park/react';
import { useLocation, useNavigate } from 'react-router-dom';
import shortid from 'shortid';
import { routes } from '../App';
import { useBookingContext } from '../context/booking-context';


export default function RoomDetail() {


    const navigate = useNavigate();
    const locaiton = useLocation();
    const { booking, setBooking } = useBookingContext();
    const room = locaiton.state.room;

    
    const add = (event) => {
        event.stopPropagation();
        setBooking(b => {
            const newBooking = {...b};
            if(!newBooking.rooms[room.id]){
                newBooking.rooms[room.id] = { count: 1 };
            }else{
                newBooking.rooms[room.id].count = (newBooking.rooms[room.id]?.count ?? 0) + 1;
            }
            return newBooking;
        })
    }

    const minus = (event) => {
        event.stopPropagation();
        setBooking(b => {
            const newBooking = {...b};
            if(!newBooking.rooms[room.id]){
                newBooking.rooms[room.id] = { count: 0 };
            }else{
                newBooking.rooms[room.id].count = (newBooking.rooms[room.id]?.count ?? 0) - 1;
                if(newBooking.rooms[room.id].count <= 0) newBooking.rooms[room.id].count = 0;
            }
            return newBooking;
        })
    } 


    return (
        <motion.div
            key={routes.ReservationHistoryDetail.path}
            initial={{ translateX: '10rem', opacity: 0 }}
            animate={{ translateX: '0rem', opacity: 1 }}
            exit={{ translateX: '10rem', opacity: 0 }}
            transition={{ type: 'tween' }}
            className='flex flex-col h-full p-3 gap-3'
        >
            <div className='flex flex-col h-full gap-3'>
                <Carousal />
                <div className='flex justify-between items-center'>
                    <div className='flex flex-col'>
                        <div className='font-bold text-[1.2rem]'>Standard room</div>
                        <div>{'70,000MMK / per night'}</div>
                        <div><span className='text-[1.2rem]'>30</span> <span className='text-[0.9rem]'>Sqft</span></div>
                    </div>
                    <div className='flex gap-5 px-5'>
                        <div className='flex flex-col items-center'>
                            <div className='font-bold'>Adult</div>
                            <div>2</div>
                        </div>
                        <div className='flex flex-col items-center'>
                            <div className='font-bold'>Child</div>
                            <div>2</div>
                        </div>
                    </div>
                </div>
                <div className='text-black/50'>
                    A full length glass window offers you the wide view of surroundings of Prince Hotel. Each room includes such as luxury bedding, soothing rainfall shower, a flat-screen smart TV, minibar, well-lit work desk and high-speed internet access.
                </div>
                <div>
                    <div className='font-bold'>Benefits</div>
                    <ul className='text-[0.9rem]'>
                        <li className='flex items-center gap-2'> <Check size={15} /> High speed Wifi</li>
                        <li className='flex items-center gap-2'><Check size={15} /> City view</li>
                    </ul>
                </div>
            </div>
            <div className='flex gap-3 items-center'>
                <div className='flex gap-3 w-full'>
                    <button onClick={() => navigate(-1)} className=' w-full py-3 rounded-2xl shadow-2xl shadow-black/5 cursor-pointer bg-black text-white font-bold'>Back</button>
                </div>
                <div className='flex gap-3 items-center justify-center w-[40%]'>
                    <div className='p-1 border rounded-full' onClick={add}><Plus theme="outline" size="20"/></div>
                    <div className='font-bold'>{booking.rooms[room.id]?.count ?? 0}</div>
                    <div className='p-1 border rounded-full' onClick={minus}><Minus theme="outline" size="20"/></div>
                </div>
            </div>
        </motion.div>
    )
}