import { init } from './init';
import { onInvokeFinished } from './bridges';
import { subscribeHandler } from './bridges';
import APIS from './apis';

// 执行初始化逻辑
document.readyState !== 'loading'
  ? init()
  : document.addEventListener('DOMContentLoaded', init);

/**
 * Support use ma.api from window
 */
window.ma = APIS;

window.viewLayer = {
  onInvokeFinished,
  subscribeHandler
};

export default APIS;
