import MacleJSBridge from 'src/bridges';
import { APINames } from 'src/shared';
import { GeneralInvokeOption } from 'src/types';

export interface GetLocalImgDataOption extends Partial<GeneralInvokeOption> {
  path: string;
}

// 获取本地base64图片
export function getLocalImgData(option?: GetLocalImgDataOption) {
  if (!option?.path) {
    console.error(
      'getLocalImgData fail,parameter error:parameter.path should be String instead of Undefined.'
    );
    return;
  }
  MacleJSBridge.invokeMethod(APINames.Native.GET_LOCAL_IMG_DATA, option);
}
