import MacleJSBridge from 'src/bridges';
import { APINames } from 'src/shared';
import { GeneralInvokeOption } from 'src/types';

/**
 * ```js
 *   ma.getLocation({
 *     success(res) {},
 *     fail(res) {},
 *     complete(res) {},
 *   })
 * ```
 */

// 获取当前地理位置信息
export function getLocation(option?: GeneralInvokeOption) {
  MacleJSBridge.invokeMethod(APINames.Native.GET_LOCATION, option);
}
