import { APINames, getGlobal, TABBAR } from 'src/shared';
import { IAnyStrObject, TabBar } from 'src/types';
import {
  NavigateToOption,
  RedirectToOption,
  SwitchTabOption,
  ReLaunchOption
} from './miniprogram';

/**
 * fix route
 * @param currentRoute 当前路由
 * @param toUrl 目标 url
 */
export function getRealRoute(
  currentRoute: string,
  toUrl: string,
  isHtml: boolean = true
): string {
  let fixedUrl = toUrl;

  if (isHtml) {
    fixedUrl = addHTMLSuffix(fixedUrl);
  }

  if (fixedUrl.startsWith('/')) {
    return fixedUrl.substr('/'.length);
  }

  if (fixedUrl.startsWith('./')) {
    return getRealRoute(currentRoute, fixedUrl.substr('./'.length), false);
  }

  // 移除 url 前面的 ../ 并记录其位置
  let index;
  let urlArrLength;
  const urlArr = fixedUrl.split('/');
  for (
    index = 0, urlArrLength = urlArr.length;
    index < urlArrLength && urlArr[index] === '..';
    index++
  );
  urlArr.splice(0, index);
  const pathArr = currentRoute.length > 0 ? currentRoute.split('/') : [];
  pathArr.splice(pathArr.length - index - 1, index + 1);
  return pathArr.concat(urlArr).join('/');
}

export function encodeUrlQuery(url: string): string {
  const urlArr = url.split('?');
  const [urlPath, queryUrl] = urlArr;
  if (!queryUrl) {
    return url;
  }
  const queryParams = queryUrl.split('&').reduce((res: IAnyStrObject, cur) => {
    if (typeof cur === 'string' && cur.length > 0) {
      const curArr = cur.split('=');
      const key = curArr[0];
      const value = curArr[1];
      res[key] = value;
    }
    return res;
  }, Object.create(null));
  const urlQueryArr = [];
  for (const i in queryParams) {
    urlQueryArr.push(i + '=' + encodeURIComponent(queryParams[i]));
  }
  return urlQueryArr.length > 0 ? urlPath + '?' + urlQueryArr.join('&') : url;
}

export function addHTMLSuffix(url: string): string {
  const urlArr = url.split('?');
  urlArr[0] += '.html';
  return urlArr[1] === undefined ? urlArr[0] : `${urlArr[0]}?${urlArr[1]}`;
}

export function checkUrl(
  apiName: string,
  options:
    | NavigateToOption
    | RedirectToOption
    | SwitchTabOption
    | ReLaunchOption
): boolean {
  const tabBar = getGlobal(TABBAR) as TabBar | null;
  if (tabBar === null) {
    return true;
  }

  if (options.url.startsWith('/')) {
    options.url = options.url.slice('/'.length);
  }

  // 目标 url 是否在 tabbar 中
  const inTabBar = ((url: string) =>
    tabBar?.list.find(e => url.indexOf(e.pagePath) === 0))(options.url);

  // navigateTo 和 redirectTo API 禁止跳转到 tabbar 页面
  if (
    apiName === APINames.MiniProgram.NAVIGATE_TO ||
    apiName === APINames.MiniProgram.REDIRECT_TO
  ) {
    if (inTabBar) {
      console.error(
        apiName,
        options,
        `${apiName}: can not ${apiName} to a tabbar page`
      );
      return false;
    }
  }

  // switchTab 禁止跳转到非 tabbar 页面
  if (apiName === APINames.MiniProgram.SWITCH_TAB) {
    if (!inTabBar) {
      console.error(
        apiName,
        options,
        `${apiName}: can not ${apiName} to a non-tabbar page`
      );
      return false;
    }
  }
  return true;
}
