import MacleJSBridge from 'src/bridges';
import { APINames } from 'src/shared';
import { GeneralInvokeOption } from 'src/types';

export interface OpenDocumentOption extends Partial<GeneralInvokeOption> {
  filePath: string;
  fileType: string;
}

// 打开文件，预览文件
export function openDocument(option?: OpenDocumentOption) {
  if (!option?.filePath) {
    console.error(
      'openDocument fail,parameter error:parameters.filePath should be String instead of Undefined.'
    );
    return;
  }
  const validFileType: Array<string> = [
    'doc',
    'docx',
    'xls',
    'xlsx',
    'ppt',
    'pptx',
    'pdf'
  ];
  if (option.fileType && validFileType.indexOf(option.fileType) === -1) {
    console.error(
      'openDocument fail,parameter error:parameter.fileType is invalid value.'
    );
    return;
  }
  MacleJSBridge.invokeMethod(APINames.Native.OPEN_DOCUMENT, option);
}
