import { motion, AnimatePresence } from 'framer-motion';
import { useEffect, useState, useRef } from 'react';
import { Check } from '@icon-park/react';
import shortid from 'shortid';

export default function OptionSelect({ items = [
    { label: 'Standard room', id: shortid() },
    { label: 'Grand deluxe room', id: shortid() },
    { label: 'Superior room', id: shortid() },
    { label: 'Junior suite room', id: shortid() }
] }) {

    const ref = useRef();
    const [show, setShow] = useState(false);
    const [selectedItem, setSelectedItem] = useState([]);


    useEffect(() => {
        if (show) ref.current.focus();
    }, [show])

    return (
        <div className="relative">
            <div onClick={() => setShow(sh => !sh)} className='bg-white/90 rounded-xl max-h-[10rem] overflow-y-auto'>
                {
                    selectedItem.length == 0 && (
                        <div className='text-[0.8rem] px-2 py-3 text-black/40'>Click to Select room type</div>
                    )
                }
                {
                    selectedItem.map((item, index) => {
                        return (
                            <span key={index} className='py-1 px-2 bg-primary/40 rounded-md mx-1 my-1 inline-block'>{item.label}</span>
                        )
                    })
                }
            </div>
            <AnimatePresence>
                {
                    show && (
                        <motion.div
                            ref={ref}
                            initial={{ scale: 0 }}
                            animate={{ scale: 1 }}
                            exit={{ scale: 0 }}
                            transition={{ duration: 0.2, type: 'just' }}
                            className="bg-white absolute origin-top p-3 w-full rounded-xl shadow-xl shadow-black/10 outline-none z-50 max-h-[15rem] overflow-y-auto"
                            tabIndex={1}
                            onBlur={() => setShow(false)}>
                            <div className='flex gap-2 items-center hover:bg-black/10 justify-between rounded-lg py-1 px-1 cursor-pointer' onClick={() => {
                                setSelectedItem(prevItems => {
                                    if(prevItems.length == items.length) return [];
                                    return items;
                                })
                            }}>
                                <div className='font-bold'>All type</div>
                                {selectedItem.length == items.length && <Check size={20} strokeWidth={6} className='text-green-500' />}
                            </div>
                            {
                                items.map((item, index) => {
                                    const isExist = selectedItem.filter(i => i.id == item.id);
                                    return (
                                        <div key={item.id} className='flex gap-2 items-center justify-between py-1 cursor-pointer' onClick={() => {
                                            setSelectedItem(prevItem => {
                                                const isExist = prevItem.filter(i => i.id == item.id);
                                                if (isExist.length == 0) {
                                                    return [...prevItem, item];
                                                }
                                                return prevItem.filter(i => i.id != item.id);
                                            })
                                        }}>
                                            <div>{item.label}</div>
                                            {isExist.length != 0 && <Check size={20} className='text-green-500' />}
                                        </div>
                                    )
                                })
                            }
                        </motion.div>
                    )
                }
            </AnimatePresence>
        </div>
    )
}